.low_footer{
    background-color: #282623;
    padding: 35px 0;
    /* background-color: #FAF8F7; */
  }

.low_footer h2{
  margin: 0 0 30px;
  text-align: center;
  color: #FFFF;
  font-size: 2.6rem;
}
.cardsContainer{
  display: grid;
  grid-template-columns: repeat( auto-fit , minmax(320px, 1fr ));
  justify-items: center;
  row-gap: 20px;
  column-gap: 15px;

}

.topicsCard{
  padding: 10px 15px;
  width: 310px;
  height: 75px;
  border-radius: 4px;
  font-size: 1.6rem;
  color: #FFFF;
  background-color: #201E1C;
  font-weight: bold;
  box-shadow: 0px 4px 8px 0px #00000029;
}

.icon_config{
  display: block;
  margin-bottom: 5px;
  width: 23px;
  height: 23px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}


@media screen and (max-width: 640px){
  .low_footer h2{
    font-size: 2.0rem;
  }
}